<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title class="text-bold">Admin Login</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    prepend-icon="person"
                    name="user_name"
                    label="User Name"
                    type="user_name"
                    v-model="details.user_name"
                  ></v-text-field>

                  <v-text-field
                    id="password"
                    prepend-icon="lock"
                    name="password"
                    label="Password"
                    type="password"
                    v-model="details.password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="loginAdmin">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  export default {
    data: () => ({
      drawer: null,
      details: {}
    }),

    props: {
      source: String
    },
    methods: {
      loginAdmin() {
        BlockToast.fire({
          text: "Verifying credentials...."
        });

        axios.post("/admin/login", { ...this.details }).then(rsp => {
          if (rsp.status == 200) {
            location.reload();
          }
        });
      }
    }
  };
</script>



